'use client';

import React, { createContext, useContext, useState, useCallback, useRef, useEffect } from 'react';


/**
 * Global variable for non-React access to app data.
 * Useful for legacy scripts or outside of React tree.
 */
export const appDataStore = {
  home: null,
  about: null,
  cv1: null,
  cv2: null,
  exhibitions: null,
  works: null,
  loading: false,
  error: null,
};

// Create Context
const AppDataContext = createContext();

/**
 * Preload media (images) from the fetched data to improve UX.
 * @param {Object} appData - The fetched application data.
 */
const preloadMedia = (appData) => {
  const urls = [];
  
  // 1. Home Image
  if (appData.home?.link) urls.push(appData.home.link);
  
  // 2. Exhibition Images
  if (Array.isArray(appData.exhibitions)) {
    appData.exhibitions.forEach(ex => {
      if (ex.link) urls.push(ex.link);
      if (Array.isArray(ex.images)) {
        ex.images.forEach(img => {
          if (img.link) urls.push(img.link);
        });
      }
    });
  }
  
  // 3. Works Images
  if (Array.isArray(appData.works)) {
    appData.works.forEach(work => {
      if (work.link) urls.push(work.link);
    });
  }

  // Deduplicate and preload
  const uniqueUrls = [...new Set(urls)];
  uniqueUrls.forEach(url => {
    // Basic check: preload only if it looks like an image or not explicitly mp4
    const isImage = /\.(jpg|jpeg|png|webp|gif|svg)$/i.test(url) || !url.includes('.mp4');
    
    if (isImage) {
      const img = new Image();
      img.src = url;
    }
  });
};

/**
 * AppDataProvider Component
 * Manages global application state and data fetching.
 */
export const AppDataProvider = ({ children }) => {
  
  const defaultData = {
    home: null,
    about: null,
    cv1: null,
    cv2: null,
    exhibitions: null,
    works: null,
    loading: false,
    error: null,
  };

  const [data, setData] = useState(defaultData);
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isRefreshing, setIsRefreshing] = useState(false); // ÏàòÎèô ÏÉàÎ°úÍ≥†Ïπ® ÏÉÅÌÉú Ï∂îÍ∞Ä
  const isLoading = useRef(false);

  const toggleMenu = useCallback(() => {
    setIsMenuOpen(prev => !prev);
  }, []);

  /**
   * Fetches all required data from the API.
   * @param {boolean} force - If true, forces a fresh fetch (e.g. for manual refresh).
   */
  const fetchAllData = useCallback(async (force = false) => {
    if (isLoading.current) return;
    
    // If we already have data and it's not a forced refresh, we can skip
    if (!force && data.home && data.exhibitions) return;
    
    isLoading.current = true;
    setData(prev => ({ ...prev, loading: true }));
    if (force) setIsRefreshing(true); // Í∞ïÏ†ú ÏÉàÎ°úÍ≥†Ïπ® ÏãúÏóêÎßå ÌôúÏÑ±Ìôî

    try {
      console.log(force ? "üîÑ Forcing fresh data fetch..." : "üîÑ Fetching fresh data from server...");
      
      const safeFetch = async (url) => {
        try {
          const fetchUrl = force ? `${url}?force=true&t=${Date.now()}` : url;
          const res = await fetch(fetchUrl).catch(err => {
            console.error(`Network error for ${url}:`, err);
            return null;
          });

          if (!res) return null;
          if (!res.ok) return null;
          return await res.json();
        } catch (e) {
          console.error(`Error for ${url}:`, e);
          return null; 
        }
      };

      const [homeRes, aboutRes, cv1Res, cv2Res, exhibitionsRes, worksRes] = await Promise.all([
        safeFetch("/api/getHome"),
        safeFetch("/api/getAbout"),
        safeFetch("/api/getCV1"),
        safeFetch("/api/getCV2"),
        safeFetch("/api/getExhibition"),
        safeFetch("/api/getWorks"),
      ]);

      const newData = {
        home: homeRes,
        about: aboutRes,
        cv1: cv1Res,
        cv2: cv2Res,
        exhibitions: exhibitionsRes,
        works: worksRes,
      };

      const finalState = { ...newData, loading: false, error: null };
      setData(finalState);
      Object.assign(appDataStore, finalState); // Update global store

      // Preload media after fetch
      preloadMedia(newData);

    } catch (error) {
      console.error('Failed to fetch app data:', error);
      const errorState = { loading: false, error: error.message };
      setData(prev => ({ ...prev, ...errorState }));
      Object.assign(appDataStore, { ...data, ...errorState });
    } finally {
      isLoading.current = false;
      setIsRefreshing(false); // Î°úÎî© Ï¢ÖÎ£å Ïãú Ìï¥Ï†ú
    }
  }, [data]);

  const refreshData = useCallback(() => {
    return fetchAllData(true);
  }, [fetchAllData]);

  // Effect: Preload media if data is already available
  useEffect(() => {
    if (data.home || data.exhibitions) {
      preloadMedia(data);
    }
  }, [data]);

  const value = {
    ...data,
    isMenuOpen,
    isRefreshing, // ÏÉàÎ°úÏö¥ ÏÉÅÌÉú Ï†ÑÎã¨
    toggleMenu,
    fetchAllData,
    refreshData,
  };

  return (
    <AppDataContext.Provider value={value}>
      {children}
    </AppDataContext.Provider>
  );
};

// Hook for using context
export const useAppData = () => {
  const context = useContext(AppDataContext);
  if (!context) {
    throw new Error('useAppData must be used within AppDataProvider');
  }
  return context;
};

// Access global store
export const getAppData = () => appDataStore;
